/* stylelint-enable color-no-hex */
html {
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji";
  font-size: 15px;
  line-height: 1.5;
  color: #4a5568;
}
@media (min-width: 426px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

main {
  margin: 2em 1em;
}

a {
  color: #63b3ed;
  text-decoration: none;
}
a:focus {
  outline: none;
}
a:hover, a:focus {
  text-decoration: underline;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #1a202c;
}

.logo {
  display: inline-flex;
  font-size: 2rem;
  line-height: 1.5;
  font-family: "Lexend Deca", Helvetica, sans-serif;
  border: 2px dashed #9f7aea;
  margin-bottom: 0;
}
.logo__square {
  height: 1.5em;
  width: 1.5em;
  border-right: 2px dashed #63b3ed;
}
.logo__title {
  padding: 0 0.5em;
  white-space: nowrap;
}
@media (min-width: 769px) {
  .logo {
    font-size: 4rem;
  }
}

/* Code */
code,
pre {
  border-radius: 0.3em;
  background-color: #f7fafc;
}

/** Inline code **/
code {
  padding: 0.3em 0.5em;
  font-size: 0.85rem;
  font-weight: 300;
  text-transform: none;
  font-family: sfmono-regular, consolas, "Liberation Mono", menlo, monospace;
}

/** Block of code **/
pre {
  line-height: 1.2;
  padding: 1.5em;
  tab-size: 2;
  overflow-x: auto;
  /* stylelint-disable-next-line selector-max-type */
  /* stylelint-disable-next-line selector-max-type */
}
pre code {
  background-color: transparent;
  padding: 0;
}
pre u {
  text-decoration: none;
}

.code-block {
  background-color: #2d3748;
  color: #93ddfd;
}

/* Colors */
.color-primary {
  color: #9f7aea;
}

.color-secondary {
  color: #63b3ed;
}

.color-tertiary {
  color: #4fd1c5;
}

.color-r {
  color: #ff8383;
}

.color-g {
  color: #b5f4a5;
}

.color-y {
  color: #ffe484;
}

.highlight {
  padding: 0 0.15em;
  border-radius: 0.15em;
}
.highlight:hover {
  text-decoration: none;
}
.highlight-primary {
  background-color: #9f7aea;
  color: #fff;
}
.highlight-secondary {
  background-color: #63b3ed;
  color: #fff;
}
.highlight-tertiary {
  background-color: #4fd1c5;
  color: #fff;
}

/* hr */
hr {
  height: 0;
  width: 100%;
  margin: 3em 0;
  border: 0;
  border-bottom: 2px solid #edf2f7;
}

.box {
  display: inline-block;
  height: 100%;
  width: 100%;
  padding: 0.5em;
  text-align: center;
  background-color: #edf2f7;
}

/* Grid */
.toggle-grid-debug {
  display: inline-block;
  position: fixed;
  bottom: 1em;
  right: 1em;
  color: #9f7aea;
  background: #fff;
  border: 2px dashed;
  white-space: nowrap;
  cursor: pointer;
  padding: 1em;
  animation: slide-in 0.4s ease-out both;
  z-index: 1;
}
@keyframes slide-in {
  from {
    opacity: 0;
    transform: translateY(100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.toggle-grid-debug:hover, .toggle-grid-debug:focus {
  opacity: 0.8;
}

.toggle-grid-debug-checkbox {
  margin: 0 0.2em 0 0;
  vertical-align: middle;
}

/* Anchor */
.anchor {
  position: relative;
}
.anchor-link {
  position: absolute;
  right: 100%;
  padding-right: 0.25rem;
  font-weight: 300;
  font-style: normal;
  opacity: 0;
}
.anchor-link:focus {
  opacity: 1;
  text-decoration: none;
}
.anchor:hover .anchor-link {
  opacity: 1;
  text-decoration: none;
}

/* Counters */
.counter {
  counter-reset: index-1;
}
.counter .counter-index-1 {
  counter-increment: index-1;
  counter-reset: index-2;
}
.counter .counter-index-1::before {
  /* stylelint-disable-next-line max-nesting-depth */
  content: counter(index-1)"." " ";
}
.counter .counter-index-2 {
  counter-increment: index-2;
  counter-reset: index-3;
}
.counter .counter-index-2::before {
  /* stylelint-disable-next-line max-nesting-depth */
  content: counter(index-1)"."counter(index-2)"." " ";
}
.counter .counter-index-3 {
  counter-increment: index-3;
  counter-reset: index-4;
}
.counter .counter-index-3::before {
  /* stylelint-disable-next-line max-nesting-depth */
  content: counter(index-1)"."counter(index-2)"."counter(index-3)"." " ";
}

/* Github Corner */
.github-corner {
  fill: #63b3ed;
  color: #fff;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 1;
}

.github-corner:hover .octo-arm {
  animation: octocat-wave 560ms ease-in-out;
}

@keyframes octocat-wave {
  0%, 100% {
    transform: rotate(0);
  }
  20%, 60% {
    transform: rotate(-25deg);
  }
  40%, 80% {
    transform: rotate(10deg);
  }
}
@media (max-width: 500px) {
  .github-corner:hover .octo-arm {
    animation: none;
  }

  .github-corner .octo-arm {
    animation: octocat-wave 560ms ease-in-out;
  }
}